<template>
    <div id="app">
        <Heard/>
        <!--        <keep-alive>-->
        <router-view v-if="isRouterAlive"/>
        <!--        </keep-alive>-->

    </div>
</template>

<script>
    import Heard from "./components/Home/Heard";

    export default {
        components: {Heard},
        provide() {
            return {reload: this.reload}
        },
        data() {
            return {isRouterAlive: true}
        },
        methods: {
            reload() {
                this.isRouterAlive = false;
                this.$nextTick(function () {
                    this.isRouterAlive = true
                })
            },
        },

    }
</script>


<style lang="less">
    #app {
        position: relative;
    }


</style>