<template>
    <div class="col-lg-8 col-md-12  col-sm-12  ">
        <div v-for="(Item,key,index) in ListData" :key="index">
            <p class="Top-time" style="color:#595959">{{key}}</p>
            <div class="item" v-for="(item,index) in Item" :key="index">
                <div @click="content(item.id)">
                    <p>{{item.title}}</p>
                </div>
                <span>发布于：</span>
                <span>{{item.createTime}}</span>
            </div>
        </div>
        <div class="paging ">
            <nav>
                <ul class="pagination justify-content-center ">
                    <li v-for="(item,index) in AllCount" :key="index"
                       class="page-item"
                        @click="Page(item)">
                        <a class="page-link"
                           :class="data.startPage===item ? 'page-link activeItem':'page-link'"
                        >{{item}}</a>
                    </li>
                </ul>
            </nav>
        </div>
        <router-view/>
    </div>
</template>

<script>
    import {mapMutations} from 'vuex';
    import {getListData} from "../../../serve";
    // import {getListData} from './../serve/index';


    export default {
        name: "Left",
        inject: ['reload'],
        data() {
            return {
                ListData: '',
                data: {
                    startPage: 1,
                    count: 1
                },
                AllCount: '',//归档个数
                tab: ''
            }
        },
        created() {
            const {data} = this;
            let arr = encodeURI(JSON.stringify(data));
            this.getList(arr, (data, ListData) => {
                if (data === 200) {
                    this.ListData = ListData.list;
                    this.AllCount = ListData.allCounts;
                }
            });
        },
        methods: {
            ...mapMutations(['GETLIST_DATA']),
            //请求数据列表
            getList(arr, callback) {
                getListData(arr, callback).then((res) => {
                    // console.log(res);
                    if (res.code === 200) {
                        let ListData = res.data;
                        this.GETLIST_DATA({ListData});
                        callback && callback(res.code, ListData)
                    }

                }).catch((err) => {
                    console.log(err);
                })
            },
            content(data) {
                // :to="{name: 'Detail', query:{ id: item.id}}"
                this.$router.push({path: '/Detail', query: {id: data}});
                // this.$router.go('/Detail');
            },
            //分页
            Page(page) {
                this.data.startPage = page;
                const {data} = this;
                this.tab = page;
                let arr = encodeURI(JSON.stringify(data));
                this.getList(arr, (data, ListData) => {
                    if (data === 200) {
                        this.ListData = ListData.list;
                    }
                });
            },
        }


    }
</script>

<style scoped>
    .Top-time {
        font-size: 1.9rem;
        font-weight: 700;
        text-align: center;
    }

    .item {
        padding-top: 15px;
        padding-bottom: 15px;
        border-bottom: 1px #eee solid;
        line-height: 2.5rem;
    }

    .item > div {
        text-decoration: none;
    }

    .item > div > p {
        font-size: 1.8rem;
        color: #565656;
        font-weight: 200;
    }

    .item > div > p:hover {
        color: #0085a1;
    }

    .paging {
        margin-top: 20px;
        margin-bottom: 30px;
    }

    .activeItem {
        background-color: #d4d4d4 !important;
    }
</style>