<template>
    <div class="col-lg-8 col-md-12  col-sm-12  ">

        <div class="Article" v-for="(item,index) in HomeData" :key="index">
            <div @click="content(item.id)">
                <span class="title">{{item.title}}</span>
                <div class="over">
                    <p class="overflow">{{item.abstractInfo}}</p>
                </div>
            </div>
            <div class="time">
                <span>发布于：</span>
                <span>{{item.createTime}}</span>
            </div>
        </div>

        <div class="Fenye">
            <nav aria-label="Page navigation example">
                <ul class="pagination justify-content-center">
                    <li class="page-item " @click="Previouspag">
                        <a class="page-link" type="button">上一页</a>
                    </li>
                    <li class="page-item" v-for="(item,index) in pageCount"
                        :key="index"

                    >
                        <a class="page-link"
                           @click="selectOne(item)"
                           :class="item==active ? 'active':'a'"
                        >{{item}}</a>
                    </li>
                    <li class="page-item" @click="Addpag">
                        <a class="page-link" type="button">下一页</a>
                    </li>
                </ul>
            </nav>
        </div>
        <!--        <p>{{pag}}</p>-->
    </div>
</template>

<script>
    // import Link from "../../../views/Link";
    import {mapMutations} from 'vuex'
    import {getHomeData} from "../../../serve/index";


    export default {
        name: "Left",
        inject: ['reload'],
        props: {
            pag: {type: Number, require: true}
        },
        data() {
            return {
                HomeData: '',
                pageCount: '',
                data: {
                    startPage: 1,
                    count: 10
                },
                startPage: '',
                active: this.$route.query.pag
            }
        },
        created() {
            // console.log(this.$route.query.pag);
            this.active = this.$route.query.pag;
            // console.log(this.active);
            this.data.startPage = this.$route.query.pag;
            let arr = encodeURI(JSON.stringify(this.data));
            this.getHome(arr, (data, homeData, pageCount) => {
                if (data === 200) {
                    this.HomeData = homeData;
                    this.pageCount = pageCount;
                }
            });
        },
        methods: {
            ...mapMutations(['GETHOME_LEFT_DATA']),
            //获取文章列表
            getHome(arr, callback) {
                getHomeData(arr, callback).then((res) => {
                    if (res.code === 200) {
                        let homeData = res.data.list;
                        // console.log(homeData);
                        let pageCount = res.data.pageCount;
                        this.GETHOME_LEFT_DATA({homeData, pageCount});
                        callback && callback(res.code, homeData, pageCount);
                    }
                }).catch((err) => {
                    console.log(err);
                });
            },
            //进入文章详情页
            content(data) {
                this.$router.push({path: '/Detail', query: {id: data}});
            },
            //下一页
            Addpag() {
                this.data.startPage += 1;
                if (this.data.startPage > this.pageCount) {
                    this.data.startPage = this.pageCount;
                    return
                }
                let data = this.data.startPage;
                // console.log(data);
                this.$router.push({
                    path: '/pages', query: {pag: data}
                });
                this.reload();
            },
            //上一页
            Previouspag() {
                this.data.startPage -= 1;
                if (this.data.startPage < 1) {
                    this.data.startPage = 1;
                    return
                }
                let data = this.data.startPage;
                console.log(data);
                this.$router.push({
                    path: '/pages', query: {pag: data}
                });
                this.reload();

            },
            selectOne(data) {
                if (data === this.$route.query.pag) {
                    return
                }
                this.$router.push({
                    path: '/pages', query: {pag: data}
                });
                this.reload();
            }
        }
    }
</script>

<style scoped>
    .Article {
        border-bottom: 1px solid #efefef;
        margin-bottom: 1rem;

    }

    .Article > a {
        text-decoration: none;
    }

    .title {
        font-size: 1.75rem;
        cursor: pointer;
        color: #333;
    }

    .overflow {
        cursor: pointer;
        width: 100%;
        margin-top: 1rem;
        line-height: 2rem;
        /*display: -webkit-box;*/
        /*-webkit-box-orient: vertical;*/
        /*-webkit-line-clamp: 4;*/
        /*text-align: center;*/
        /*overflow: hidden;*/
        color: #575757;
        transition: all ease-in 0.2s;
    }

    .over > p {
        width: 100%;
        margin-top: 1rem;
        line-height: 2rem;
        /*display: -webkit-box;*/
        /*-webkit-box-orient: vertical;*/
        /*-webkit-line-clamp: 4;*/
        /*text-align: center;*/
        /*overflow: hidden;*/
        color: #575757;
        transition: all ease-in 0.2s;
    }

    .over :hover {
        color: #0085a1 !important;
        /*font-size: 50px;*/
    }

    .time {
        margin-top: 1rem;
        margin-bottom: 2rem;
        color: #b8b8b8;
    }

    .Fenye {
        margin-top: 30px;
        margin-bottom: 50px;
    }

    .active {
        background-color: #b5b5b5;
    }
</style>
