<template>
    <div id="Center" class=" container-fuild banner ">
        <div id="Center-flex" class="jumbotron text-center h-100 bg-dark text-white">
            <p class="Center-one">{{ContentData.title}}</p>
            <p class=" Center-two  m-4 ">发布于 {{ContentData.createTime}}</p>
        </div>
    </div>
</template>

<script>
    import {mapMutations} from "vuex";
    import {getContentData} from "../../serve";

    export default {
        name: "Curtain",
        inject: ['reload'],
        data() {
            return {
                ContentData: '',
            }
        },
        created() {
            const Id = this.$route.query.id;
            this.getDetailData(Id, (stutas, data) => {
                if (stutas === 200) {
                    this.ContentData = data;
                    // this.reload();
                }
            });
        },
        methods: {
            ...mapMutations(['GETCONTENT_DATA']),
            getDetailData(id, callback) {
                getContentData(id, callback).then((res) => {
                    // console.log(res);
                    if (res.code === 200) {
                        let data = res.data;
                        this.GETCONTENT_DATA({data});
                        callback && callback(res.code, data);
                    }
                }).catch((err) => {
                    console.log(err);
                })
            }
        }
    }
</script>

<style scoped>
    #Center {
        /*background-color: red;*/
        height: 25rem;
        width: 100%;
        margin-bottom: 2rem;
    }

    #Center-flex {
        width: 100%;
        height: 100%;
        display: flex;
        /*align-items: center;*/
        flex-direction: column;
        justify-content: center;
    }

    .Center-one {
        font-weight: 600 !important;
        font-size: 4rem;
    }

    .Center-two {
        font-weight: 400 !important;
        font-size: 2rem;
    }


</style>