<template>
    <div class="col-lg-8 col-md-12  col-sm-12  ">

        <div class="Article" v-for="(item,index) in resultData" :key="index">
            <a @click="pushDetail(item.id)">
                <span class="title">{{item.title}}</span>
                <div class="over">
                    <p class="overflow">{{item.abstractInfo}}</p>
                </div>
            </a>
            <div class="time">
                <span>发布于：</span>
                <span>{{item.createTime}}</span>
            </div>
        </div>


    </div>
</template>

<script>
    // import Link from "../../../views/Link";
    import {mapMutations} from "vuex";
    import {getTagData} from "./../../../serve/index";

    export default {
        name: "Left",
        data() {
            return {
                resultData: '',
                data: {
                    startPage: 1,
                    count: 100,
                }
            }
        },
        created() {
            const Id = this.$route.query.id;
            const {data} = this;
            let arr = encodeURI(JSON.stringify(data));
            this.getTagsData(arr, Id, (data, ResultData) => {
                if (data === 200) {
                    this.resultData = ResultData;
                    // this.reload();
                }
            });
        },

        methods: {
            ...mapMutations(['GETTAGS_DATA']),
            getTagsData(data, id, callback) {
                getTagData(data, id, callback).then((res) => {
                    // console.log(res);
                    if (res.code === 200) {
                        const ResultData = res.data.list;
                        this.GETTAGS_DATA({ResultData});
                        callback && callback(res.code,ResultData);
                    }
                }).catch((err) => {
                    console.log(err);
                })
            },
            pushDetail(data){
                this.$router.push({path: '/Detail', query: {id: data}});
            }
        },


    }
</script>

<style scoped>
    .Article {
        border-bottom: 1px solid #efefef;
        margin-bottom: 1rem;

    }

    .Article > a {
        text-decoration: none;
    }

    .title {
        font-size: 1.75rem;
        cursor: pointer;
        color: #333;
    }

    .overflow {
        cursor: pointer;
        width: 100%;
        margin-top: 1rem;
        line-height: 2rem;
        /*display: -webkit-box;*/
        /*-webkit-box-orient: vertical;*/
        /*-webkit-line-clamp: 4;*/
        /*text-align: center;*/
        /*overflow: hidden;*/
        color: #575757;
        transition: all ease-in 0.2s;
    }

    .over > p {
        width: 100%;
        margin-top: 1rem;
        line-height: 2rem;
        /*display: -webkit-box;*/
        /*-webkit-box-orient: vertical;*/
        /*-webkit-line-clamp: 4;*/
        /*text-align: center;*/
        /*overflow: hidden;*/
        color: #575757;
        transition: all ease-in 0.2s;
    }

    .over :hover {
        color: #0085a1 !important;
        /*font-size: 50px;*/
    }

    .time {
        margin-top: 1rem;
        margin-bottom: 2rem;
        color: #b8b8b8;
    }
</style>