import ajax from './axios';

// 首页数据
const BASE_URL = 'http://127.0.0.1:8080';


export const getHomeData = (data) => ajax(BASE_URL + '/articles?json=' + data);
export const getHomeTagsData = (data) => ajax(BASE_URL + '/tags?json=' + data);
export const getHomeHotData = (data) => ajax(BASE_URL + '/articles/hot-articles?json=' + data);
export const getListData = (data) => ajax(BASE_URL + '/articles/archives?json=' + data);
export const getContentData = (id) => ajax(BASE_URL + '/articles/article?id=' + id);
// /articles?json={
// "startPage":1,
//     "count":1
// }&tagid=4
///标签下的文章
export const    getTagData = (data, tag) => ajax(BASE_URL + '/articles?json=' + data + '&tagid=' + tag);

//搜索下的文章
export const getSearchData = (keywords,data) => ajax(BASE_URL + '/articles?keywords=' + keywords + '&json=' + data);
