<template>
    <div class="container" id="Scrollspy" data-spy="scroll" data-target="#myScrollspy">
        <div class="row">
            <!--            //左边-->
            <div class="col-lg-12 col-md-12  col-sm-12  mb-5">
                <div class="typo">
                    <div v-html='ContentData.content'></div>
                </div>

            </div>
            <!--            右边-->

        </div>
    </div>

</template>

<script>


    import {mapMutations} from "vuex";
    import {getContentData} from "../../serve";
    import 'typocss/typo.css';

    export default {
        name: "Container",
        inject: ['reload'],
        data() {
            return {
                ContentData: '',
            }
        },
        created() {
            const Id = this.$route.query.id;
            this.getDetailData(Id, (stutas, data) => {
                if (stutas === 200) {
                    this.ContentData = data;
                    // this.reload();
                }
            });
        },
        methods: {
            ...mapMutations(['GETCONTENT_DATA']),
            getDetailData(id, callback) {
                getContentData(id, callback).then((res) => {
                    // console.log(res);
                    if (res.code === 200) {
                        let data = res.data;
                        this.GETCONTENT_DATA({data});
                        callback && callback(res.code, data);
                    }
                }).catch((err) => {
                    console.log(err);
                })
            }
        }
    }
</script>

