import {
    GETHOME_LEFT_DATA,
    GETHOME_RIGHT_TAGS,
    GETHOME_RIGHT_HOT_DATA,
    GETLIST_DATA,
    GETCONTENT_DATA,
    GETTAGS_DATA,
    GETSEARCH_DATA
} from './mutations-type'


export default {

    [GETHOME_LEFT_DATA](state, {homeData, pageCount}) {
        state.HomeData = homeData;
        state.pageCount = pageCount;

    },
    [GETHOME_RIGHT_TAGS](state, {TagsData}) {

        state.TagsData = TagsData;

    },
    [GETHOME_RIGHT_HOT_DATA](state, {RightHotData}) {

        state.RightHotData = RightHotData;

    },
    [GETLIST_DATA](state, {ListData}) {

        state.ListData = ListData;

    },
    [GETCONTENT_DATA](state, {data}) {

        state.ContentData = data;

    },
    [GETTAGS_DATA](state, {ResultData}) {

        state.ResultData = ResultData;

    },
    [GETSEARCH_DATA](state, {data}) {

        state.SearchData = data;

    },

}