                    "count": 1
                },
                keywords: '',
            }
        },
        created() {

        },
        methods: {

            _search() {
                // debugger
                if (this.keywords.trim() !== '') {
                    this.$router.push({path: '/search', query: {keywords: this.keywords}});
                    this.keywords = '';
                    // this.$router.go(0)
                    this.reload();
                }

            },
        }

    }


