<template>
    <div class="col-lg-8 col-md-12  col-sm-12  ">

        <div class="Article" v-for="(item,index) in SearchData" :key="index">
            <a @click="pushDetail(item.id)">
                <span class="title">{{item.title}}</span>

                <div class="over">
                    <p class="overflow">{{item.abstractInfo}}</p>
                </div>
            </a>
            <div class="time">
                <span>发布于：</span>
                <span>{{item.createTime}}</span>
            </div>
        </div>

    </div>
</template>

<script>
    import {mapMutations} from "vuex";
    import {getSearchData} from "../../../serve";

    export default {
        name: "Left",

        data() {
            return {
                keyword: '',
                SearchData: '',
                data: {
                    startPage: 1,
                    count: 100,
                }
            }
        },
        created() {
            this.keywords = this.$route.query.keywords;
            const {data, keywords} = this;
            let arr = encodeURI(JSON.stringify(data));
            this.getData(keywords, arr, (data, SearchData) => {
                if (data === 200) {
                    this.SearchData = SearchData;
                }
            });
        },
        methods: {
            ...mapMutations(['GETSEARCH_DATA']),
            getData(keywords, arr, callback) {
                getSearchData(keywords, arr, callback).then((res) => {
                    // console.log(res);
                    if (res.code === 200) {
                        let data = res.data.list;
                        this.GETSEARCH_DATA({data});
                        callback && callback(res.code, data);
                    }
                    // debugger;
                }).catch((err) => {
                    console.log(err);
                });
            },
            //文章详情页
            pushDetail(id) {
                this.$router.push({path: '/Detail', query: {id: id}});
            },

        },
    }
</script>

<style scoped>
    .Article {
        border-bottom: 1px solid #efefef;
        margin-bottom: 1rem;

    }

    .Article > a {
        text-decoration: none;
    }

    .title {
        font-size: 1.75rem;
        cursor: pointer;
        color: #333;
    }

    .overflow {
        cursor: pointer;
        width: 100%;
        margin-top: 1rem;
        line-height: 2rem;
        /*display: -webkit-box;*/
        /*-webkit-box-orient: vertical;*/
        /*-webkit-line-clamp: 4;*/
        /*text-align: center;*/
        /*overflow: hidden;*/
        color: #575757;
        transition: all ease-in 0.2s;
    }

    .over > p {
        width: 100%;
        margin-top: 1rem;
        line-height: 2rem;
        /*display: -webkit-box;*/
        /*-webkit-box-orient: vertical;*/
        /*-webkit-line-clamp: 4;*/
        /*text-align: center;*/
        /*overflow: hidden;*/
        color: #575757;
        transition: all ease-in 0.2s;
    }

    .over :hover {
        color: #0085a1 !important;
        /*font-size: 50px;*/
    }

    .time {
        margin-top: 1rem;
        margin-bottom: 2rem;
        color: #b8b8b8;
    }
</style>