<template>
    <div id="Right" class="d-none d-lg-block col-lg-3  ml-5  text-center ">
        <div class="top">
            <p class=" title font-weight-bold">博客导航</p>
            <img src="https://tse2-mm.cn.bing.net/th/id/OIP.ZLN97iK_1FXaSNlT-yHYUQHaHa?w=209&h=206&c=7&o=5&dpr=1.25&pid=1.7">
            <div class="Icon">

                <a href="https://weibo.com/u/5578825241?topnav=1&wvr=6&topsug=1&is_hot=1" target="_blank"><i
                        class="iconfont iconweibo-copy"></i></a>
                <a><i class="iconfont iconweixin-copy"></i></a>
                <a><i class="iconfont iconsign_qq"></i></a>
            </div>
        </div>
        <div class="center">
            <p class=" title font-weight-bold">文章标签</p>
            <div class="content">
                <a v-for="(item,index) in TagsData" :key="index">
                    <span @click="res(item.id,item.name)">{{item.name}}</span>
                </a>

            </div>
        </div>
        <div class="foot">
            <p class=" title font-weight-bold">热门文章</p>
            <div class="foot-center">
                <p v-for="(item,index) in RightHotData" :key="index">
                    <a @click="content(item.id)">
                        <span>{{item.title}}</span>
                    </a>
                </p>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapMutations} from "vuex";
    import {getHomeHotData, getHomeTagsData} from "../../../serve";

    export default {
        name: "Right",
        inject:['reload'],
        data() {
            return {
                RightHotData: '',
                TagsData: '',
                data: {
                    startPage: 1,
                    count: 10
                },
            }
        },
        created() {
            // this.RightHotData = JSON.parse(window.sessionStorage.getItem('RightHotData'));
            // this.TagsData = JSON.parse(window.sessionStorage.getItem('TagsData'));
            const {data,} = this;
            // let arr=JSON.parse(JSON.stringify(data));
            let arr = encodeURI(JSON.stringify(data));
            // this.getHome(arr);
            //首页右边标签数据
            this.getHomeTags(arr, (data, TagsData) => {
                if (data === 200) {
                    this.TagsData = TagsData;
                }
            });
            this.getHomeHot(arr, (data, RightHotData) => {
                if (data === 200) {
                    this.RightHotData = RightHotData;
                }
            });
        },
        methods: {
            ...mapMutations(['GETHOME_LEFT_DATA', 'GETHOME_RIGHT_TAGS', 'GETHOME_RIGHT_HOT_DATA']),

            getHomeTags(arr, callback) {
                getHomeTagsData(arr, callback).then((res) => {
                    if (res.code === 200) {
                        let TagsData = res.data.list;
                        this.GETHOME_RIGHT_TAGS({TagsData});
                        callback && callback(res.code, TagsData)
                    }
                }).catch((err) => {
                    console.log(err);
                });
            },
            getHomeHot(arr, callback) {
                getHomeHotData(arr, callback).then((res) => {
                    if (res.code === 200) {
                        let RightHotData = res.data;
                        this.GETHOME_RIGHT_HOT_DATA({RightHotData});
                        callback && callback(res.code, RightHotData)
                    }
                }).catch((err) => {
                    console.log(err);
                });
            },
            /// 热门文章内容
            content(data) {
                this.$router.push({path: '/detail', query: {id: data}});
            },
            //标签
            res(e, data) {
                this.$router.push({path: '/result', query: {id: e, tag: data}});
                // this.$router.go(0);
                this.reload();
            }

        }
    }
</script>

<style scoped>
    #Right {
        overflow: hidden;
        margin-bottom: 100px;
    }

    .top {
        border-top: 1px solid #efefef;
    }

    .top > img {
        width: 180px;
        height: 180px;
        margin-top: 15px;
        margin-bottom: 15px;
    }

    .title {
        margin: 18px;
    }

    .Icon {
        margin-bottom: 35px;

    }

    .Icon > a {
        padding: 5px;
        font-size: 30px;
        color: #343a40 !important;
        text-align: center;
        /*background-color: #fff;*/
        /*border-radius: 50%;*/
        margin-left: 10px;
        text-decoration: none;
    }

    .Icon > a > i {
        /*margin-left: 15px;*/
        font-size: 32px;
        transition: 0.3s ease-in all;
    }

    .Icon > a :hover {
        /*margin-left: 15px;*/
        color: #1abc9c;
    }

    .center {
        border-top: 1px solid #efefef;
        margin-bottom: 210px;
    }

    .content > span {
        transition: all 0.2s ease-in;
        clear: both;
    }

    .center .content {
        /*margin: 25px;*/
        /*padding-bottom: 55px;*/
        margin-top: 25px;
        margin-left: 25px;
        margin-bottom: 20px;

    }

    .center .content :hover {
        color: white;
        background-color: #7e9199;
    }

    .content > a {
        text-decoration: none;
        color: #1d1d1d;
    }

    .content > a > span {
        border: 1px solid #a2a2a2;
        margin: 5px;
        padding: 8px;
        font-size: 75%;
        font-weight: 700 !important;
        border-radius: 7px;
        text-align: center;
        float: left;
    }

    .foot {
        border-top: 1px solid #efefef;
        margin-top: 20px;
        clear: both;
        /*background-color: #ff4a7b;*/
    }

    .foot .foot-center {
        margin-top: 25px;
        margin-bottom: 30px;
    }

    .foot .foot-center > p {
        margin: 10px;
        color: #333;
    }

    .foot .foot-center > p > a {
        color: #333;
        line-height: 1.5rem;
        transition: all 0.2s ease-in;
    }

    .foot .foot-center > p :hover {
        color: #1abc9c;
    }
</style>